import { connect } from 'react-redux';
import { selectLocationAsync } from '../modules/setCenter';
import Map from '../components/Map';
import ListCategories from '../components/ListCategories';
import '../styles/mapContainer.scss';

const DesktopMapContainer = ({
  infos,
  centerLoca,
  selectLocationAsync
}) => {

  return (
    
    <div className="middle-wrapping">
      <div className="contents">
        <div className="left">
          <ListCategories
            infos={infos}
            onSelectLocation={selectLocationAsync}
          />
        </div>
        <div className="right">
          <div className="map-block">
            <Map
              infos={infos}
              centerLoca={centerLoca}
            />
          </div>
        </div>
      </div>
      
    </div>
  )
};

//       
export default connect(
  //        
  //   export    .
  //    state     setCenter  .
  ({ setCenter }) => ({
    infos: setCenter.infos,
    centerLoca: setCenter.centerLoca
  }),
  {
    //    
    selectLocationAsync
  }
)(DesktopMapContainer);