const setCenter = handleActions(
  {
    [SELECT_LOCATION]: (state, action) => ({
      ...state,
    }),
    [SELECT_LOCATION_SECCESS]: (state, action) => ({
      ...state,
      centerLoca: {
        latlng: [...state.infos[action.payload-1].latlng],
      }
    }),
  },
  initialState
);

export default setCenter;